body, html {
  height: 100%;
  margin: 0;
}

.game {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

.board {
  display: grid;
  grid-template-columns: repeat(15, 20px);
  gap: 5px;
}

.square {
  width: 20px;
  height: 20px;
  border: 1px solid #333;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.X {
  background-color: #f00;
}

.O {
  background-color: #0f0;
}